  --shadow-card: 0 18px 40px -32px rgba(15, 23, 42, 0.35);
}
body {
  background-color: var(--background);
  background-image: radial-gradient(
      circle at top right,
      rgba(14, 165, 233, 0.18),
      transparent 45%
    ),
    radial-gradient(
      circle at 20% 20%,
      rgba(249, 115, 22, 0.14),
